import { graphql } from "gatsby";

import React from "react";
import CommonHead from "../layout/Head";
import ProductDetailsPage from "../layout/pages/ProductDetailsPage";
import JsonLd from "../layout/jsonLd";
import { productJsonLd } from "../seo/jsonLd";
import { formatProduct } from "../graphql/products";

const PageTemplate = (props) => {
  const {
    allGoogleSpreadsheetPetitsAnimauxProducts,
    allGooglePhotosPhoto,
    allGoogleSpreadsheetPetitsAnimauxCategories,
  } = props.data;
  
  const data = allGoogleSpreadsheetPetitsAnimauxProducts.nodes[0];
  const product = formatProduct(data, allGooglePhotosPhoto.nodes);
  const category = allGoogleSpreadsheetPetitsAnimauxCategories.nodes.find(node => {
    return node.category === product.category;
  });
  const categoryName = category.title;
  const coverUrl = product.coverPhoto.file.publicURL;
  const jsonld = {
    ...productJsonLd,
    name: product.name,
    category: categoryName,
    image: coverUrl,
    description: product.description,
  }
  return (
    <>
      <ProductDetailsPage product={product} />
      <JsonLd data={jsonld} />
    </>
  )
};

export function Head(props) {
  const {
    allGoogleSpreadsheetPetitsAnimauxProducts,
  } = props.data;
  const node = allGoogleSpreadsheetPetitsAnimauxProducts.nodes[0];

  const data = {
    page: {
      name: node.name,
    }
  };

  return <CommonHead {...props} data={data} />;
}

export const query = graphql`
  query PetitsAnimaux($id: String!) {
    allGoogleSpreadsheetPetitsAnimauxProducts(filter: {id: {eq: $id}}) {
      nodes {
        name
        id
        price
        category
        description 
        materialDescription 
        etsyLink 
        coverPhotoId
        priceId 
        width
        height
        length
      }
    }

    allGoogleSpreadsheetPetitsAnimauxCategories {
      nodes {
          category
          title
      }
  }
    
    allGooglePhotosPhoto(filter: {album: {title: {eq: "P2F"} } }) {
      nodes {
        id
        file {
          childImageSharp {
            gatsbyImageData(
              height: 500
            )
          }
          publicURL
        }
        description
      }
    }

  }
`;

export default PageTemplate;