import React from "react";
import { H1, H2 } from "../../components/Header";
import MainSectionWrapper from "../../index";
import StripeBuyButton from "../../components/StripeBuyButton";
import EtsyLink from "../../components/EtsyLink";
import { GatsbyImage, getImage } from "gatsby-plugin-image";

import "./styles.scss";

const ShippingDisclaimer = () => {
    return (
        <div>
            <p><b>Made to order, will ship in 1 - 2 weeks.</b></p>
            <p>Note: Each item is made with highest care and attention to details. However due to the nature of handmade items, slight differences may show from the pictures.</p>
            <p><i>Gentle use is recommended</i></p>
        </div>
    );
}

const ProductDetailsImages = (props) => {
    const { photos, name } = props;
    const secondaryPhotos = photos.slice(1, photos.length);
    return (
        <div className="ProductDetailsPhotoContainer">
            <div className="ProductDetailsMainPhotoContainer">
                <GatsbyImage
                    key={photos[0]}
                    image={getImage(photos[0].file)}
                    alt={name}
                />
            </div>
            <div className="ProductDetailsSecondaryPhotoContainer">
                {secondaryPhotos.map(secondaryPhoto => {
                    return (
                        <div className="ProductDetailsSecondaryPhoto" key={secondaryPhoto}>
                        <GatsbyImage
                                key={secondaryPhoto}
                                image={getImage(secondaryPhoto.file)}
                                alt={name}
                            />
                    </div>
                    );
                })}
            </div>
        </div>
    );
}

const ProductDetailsInfo = (props) => {
    const {id, etsyLink, name, materialDescription, description, dimensions, priceId, price} = props;

    const flattenedDimensions = dimensions.reduce((accum, str, idx) => {
        if (idx + 1 === dimensions.length) {
            return accum + str
        }                  
        return `${accum} ${str}, `;
    }, '')

    return (
        <div className="ProductDetailsProductDetails">
            <div className="ProductDetailsPricingDetails">
                <H1>{name}</H1>
                <H2 className="Price">${price} USD</H2>

                {!etsyLink &&<StripeBuyButton priceId={priceId} id={id} />}

                {etsyLink &&
                <div className="ProductDetailsPurchaseMethods">
                    <StripeBuyButton priceId={priceId} id={id} />
                    <div className="OrDivider">
                        or
                    </div>
                    <EtsyLink etsyLink={etsyLink} />
                </div>
                }
            </div>

            <p className="ProductDescription">
                {description}
            </p>

            {dimensions && dimensions.length > 0 &&
                <p>
                    Dimensions: {flattenedDimensions}
                </p>
            }

            <p className="ProductDescription">
                {materialDescription}
            </p>

            <ShippingDisclaimer />

        </div>
    );
}

const ProductDetailsPage = (props) => {
    const { product } = props;
    const {
        coverPhoto,
        name,
        photos,
    } = product;

    return (
        <>
            <MainSectionWrapper>
                <div className="ProductDetailsPageContainer">
                    <ProductDetailsImages 
                        coverPhoto={coverPhoto} 
                        name={name}
                        photos={photos}
                    />

                    <ProductDetailsInfo
                        {...product}
                    />
                </div>

            </MainSectionWrapper>
        </>
    )
};

export default ProductDetailsPage;